.div,
.email {
    align-self: stretch;
}
.div {
    position: relative;
    font-weight: 500;
}
.email {
    border: 0;
    font-family: Roboto;
    font-size: 1rem;
    background-color: #eefaea;
    border-radius: 6px;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    padding: 1rem 0.75rem;
    align-items: center;
    justify-content: flex-start;
}
.email:focus {
    outline: 0;
}
.email1,
.email2,
.parent {
    display: flex;
    justify-content: flex-start;
}
.parent {
    width: 20.38rem;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.38rem;
}
.email1,
.email2 {
    border: 0;
    background-color: #eefaea;
    align-self: stretch;
    border-radius: 6px;
    overflow: hidden;
    flex-direction: row;
    padding: 1rem 0.75rem;
    align-items: center;
    outline: 0;
}
.email2 {
    font-family: Roboto;
    font-size: 1rem;
}
.frame-group,
.parent1 {
    width: 20.38rem;
    flex-direction: column;
    align-items: flex-start;
}
.frame-group {
    position: absolute;
    top: 0;
    left: calc(50% - 163.5px);
    display: flex;
    justify-content: flex-start;
    gap: 1.13rem;
}
.parent1 {
    gap: 0.38rem;
    font-family: Roboto;
}
.email5,
.frame-container,
.parent1,
.parent2 {
    display: flex;
    justify-content: flex-start;
}
.email5 {
    border: 0;
    font-family: Roboto;
    font-size: 1rem;
    background-color: #eefaea;
    align-self: stretch;
    border-radius: 6px;
    height: 8.38rem;
    overflow: hidden;
    flex-shrink: 0;
    flex-direction: row;
    padding: 1rem 0.75rem;
    box-sizing: border-box;
    align-items: center;
    outline: 0;
}
.frame-container,
.parent2 {
    width: 20.38rem;
    flex-direction: column;
    align-items: flex-start;
}
.parent2 {
    height: 10.13rem;
    gap: 0.38rem;
}
.frame-container {
    position: absolute;
    top: 18.19rem;
    left: calc(50% - 162.5px);
    padding: 0 0 5.19rem;
    box-sizing: border-box;
    gap: 1.13rem;
}
.frame-parent {
    position: absolute;
    top: 6.25rem;
    left: calc(50% - 162px);
    width: 20.44rem;
    height: 45.56rem;
}
.div6,
.register1 {
    position: absolute;
    top: 0;
}
.div6 {
    left: 0;
    letter-spacing: -0.01em;
    background: linear-gradient(202.17deg, #03c75a, #85c644);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.register1 {
    height: 100%;
    width: 67.89%;
    right: 15.9%;
    bottom: 0;
    left: 16.21%;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.icon-menu,
.icon-notification,
.icon-profile-circle {
    position: absolute;
    height: 47.22%;
    width: 6.12%;
    top: 27.78%;
    right: 93.88%;
    bottom: 25%;
    left: 0;
    max-width: 100%;
    overflow: hidden;
    max-height: 100%;
}
.icon-notification,
.icon-profile-circle {
    height: 41.77%;
    width: 3.73%;
    top: 38.89%;
    right: 6.36%;
    bottom: 19.34%;
    left: 89.91%;
}
.icon-profile-circle {
    height: 41.67%;
    width: 4.59%;
    right: 0;
    bottom: 19.44%;
    left: 95.41%;
}
.register-parent {
    position: absolute;
    top: 1.5rem;
    left: calc(50% - 164px);
    width: 20.44rem;
    height: 2.25rem;
    font-size: 1.88rem;
    font-family: "Zen Dots";
}
.div7,
.rankform-child {
    position: absolute;
    top: 66.81rem;
    height: 2.56rem;
}
.rankform-child {
    left: calc(50% - 163px);
    border-radius: 5px;
    background-color: #51c64d;
    width: 20.31rem;
}
.div7 {
    left: calc(50% - 61.33px);
    font-size: 0.94rem;
    font-weight: 600;
    color: #fff;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 7.69rem;
}
.rankform {
    position: relative;
    background-color: #fff;
    width: 100%;
    height: 69.81rem;
    overflow: hidden;
    text-align: left;
    font-size: 1.13rem;
    color: #636363;
    font-family: Inter;
}
@media screen and (max-width: 960px) {
    .div {
        font-size: 0.88rem;
    }
}
